import React, { useRef, useEffect, useState } from 'react';
import mapboxgl from 'mapbox-gl';
import './Mapload.css';
import * as turf from '@turf/turf';
import './Modal.css';
import AnecdoteModal from './AnecdoteModal'
import StopsModal from './StopsModal';
import SideNavbar from './menu/SideNavbar';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Music from './pages/Music';
import About from './pages/About';
 
mapboxgl.accessToken = 'pk.eyJ1IjoicHJhbm1hbjExMTAiLCJhIjoiY2treHo3dzB5MGFlZDJvbzQxMDA4cGFxeiJ9.TR0TinMi5Fn3hd8JmF_yfQ';

const Mapload = () => {
    const mapContainerRef = useRef(null);
  
    var point = {
            'type': 'FeatureCollection',
            'features': [{
                'type': 'Feature',
                'properties': {
                    'description': 'Anecdote and stuff',
                    'icon': 'bus'
                },
                'geometry': {
                    'type': 'Point',
                    'coordinates': [77.55469,12.94438]
                }
            }]
        };
 
    const [lng, setLng] = useState(77.6517347);
    const [lat, setLat] = useState(12.9679711);
    const [zoom, setZoom] = useState(13);
    const [displayMenu, setDisplayMenu] = useState(false);
  
    // Initialize map when component mounts
    useEffect(() => {
      const map = new mapboxgl.Map({
        container: mapContainerRef.current,
        style: 'mapbox://styles/pranman1110/ckljhzuuy0ibx17qo8ulkjx1m',
        center: [lng, lat],
        zoom: zoom
      });

    var routeGeoJsonData = null;
    var lineDistance = null;
    
    fetch('https://pranman11.github.io/bus-data/201.geojson')
    .then((resp) => resp.json())
    .then(function(data) {
        console.log(data);
        routeGeoJsonData = data;
        lineDistance = turf.length(routeGeoJsonData.features[0]);
        var arc = [];
        // Draw an arc between the `origin` & `destination` of the two points
        for (var i = 0; i < lineDistance; i += lineDistance / steps) {
            var segment = turf.along(routeGeoJsonData.features[0], i);
            arc.push(segment.geometry.coordinates);
        }
        
        routeGeoJsonData.features[0].geometry.coordinates = arc;
    })
    .catch(function(error) {
        console.log(error);
    });
  
      // Add navigation control (the +/- zoom buttons)
      map.addControl(new mapboxgl.NavigationControl(), 'top-right');
  
      map.on('move', () => {
        setLng(map.getCenter().lng.toFixed(4));
        setLat(map.getCenter().lat.toFixed(4));
        setZoom(map.getZoom().toFixed(2));
      });
 
        // Calculate the distance in kilometers between route start/end point.
     
        // Number of steps to use in the arc and animation, more steps means
        // a smoother arc and animation, but too many steps will result in a
        // low frame rate
        var steps = 10000;
 
        var counter = 0;
 
        map.on('load', function () {
            // Add a source and layer displaying a point which will be animated in a circle.
            map.addSource('route0001', {
                'type': 'geojson',
                'data': routeGeoJsonData
            });
            
            map.addSource('point', {
                'type': 'geojson',
                'data': point
            });
 
            map.addLayer({
                'id': 'point',
                'source': 'point',
                'type': 'symbol',
                'layout': {
                    'icon-size': 2,
                    'icon-image': 'bus',
                    'icon-rotate': ['get', 'bearing'],
                    'icon-rotation-alignment': 'map',
                    'icon-allow-overlap': true,
                    'icon-ignore-placement': true
                }
            });

            var popup = new mapboxgl.Popup();
 
            function animate() {
                    var start =
                    routeGeoJsonData.features[0].geometry.coordinates[
                        counter >= steps ? counter - 1 : counter
                    ];
                    var end =
                    routeGeoJsonData.features[0].geometry.coordinates[
                        counter >= steps ? counter : counter + 1
                    ];
                    if (!start || !end) return;
                    
                    // Update point geometry to a new position based on counter denoting
                    // the index to access the arc
                    point.features[0].geometry.coordinates =
                    routeGeoJsonData.features[0].geometry.coordinates[counter];
                    
                    // Calculate the bearing to ensure the icon is rotated to match the route arc
                    // The bearing is calculated between the current point and the next point, except
                    // at the end of the arc, which uses the previous point and the current point
                    point.features[0].properties.bearing = turf.bearing(
                        turf.point(start),
                        turf.point(end)
                    );
                    
                    // Update the source with this new data
                    map.getSource('point').setData(point);
                    map.panTo(start, {duration: 100});
                    
                    // Request the next frame of animation as long as the end has not been reached
                    if (counter < steps) {
                        requestAnimationFrame(animate);
                    }
                    
                    counter = counter + 1;
                }

                function startJourney() {
                    map.flyTo({center: point.features[0].geometry.coordinates,
                        zoom: 16,
                        speed: 0.5,
                    });
                    setTimeout(animate, 2000);

                    setDisplayMenu(true);

                    popup.remove();
                }
 
                map.on('click', 'point', function (e) {    
                    // var coordinates = e.features[0].geometry.coordinates.slice();
                    // var description = e.features[0].properties.description;
                    
                    // Ensure that if the map is zoomed out such that multiple
                    // copies of the feature are visible, the popup appears
                    // over the copy being pointed to.
                    // while (Math.abs(e.lngLat.lng - coordinates[0]) > 180) {
                        // coordinates[0] += e.lngLat.lng > coordinates[0] ? 360 : -360;
                    // }
                    
                    popup.setLngLat(point.features[0].geometry.coordinates)
                        .setHTML('<strong>Srinagar Bus Stop</strong><p>Srinagar Bus Stop, 18, 17th Main Rd, Raghavendra Block, Dasarhalli, Srinagar, Banashankari, Bengaluru, Karnataka</p><button id="start-journey">Start Journey</button>')
                        .addTo(map);
                        
                    document.getElementById('start-journey').addEventListener('click', startJourney)                        
                    });
        });
      // Clean up on unmount
      return () => map.remove();
    }, []); // eslint-disable-line react-hooks/exhaustive-deps
  
    return (
      <div>
        <Router>
            <SideNavbar />
            <Switch>
                <Route path='/about' component={About} />
                <Route path='/music' component={Music} />
            </Switch>
        </Router>
        <div className={`anecdote-modal ${displayMenu ? 'Show' : ''}`}>
            <AnecdoteModal />
        </div>
        <div className={`stops-modal ${displayMenu ? 'Show' : ''}`}>
            <StopsModal />
        </div>
        <div className='map-container' ref={mapContainerRef} />
      </div>
    );
};
 
export default Mapload;
